import * as actionTypes from '../actionTypes';
import { toast } from 'react-toastify';

const initState = {
  nothing: 'ddd',
};

function notifications(state = initState, action) {
  switch (action.type) {

    case actionTypes.CREATE_ACCOUNT_SUCCESS:
      setTimeout(
        () => {
          toast.success('      !');
        }, 0);
      return { ...state };

    case actionTypes.LOGIN_SUCCESS:
      setTimeout(
        () => {
          toast.success(' !');
        }, 0)
      return { ...state };


    case actionTypes.PAYMENT_IGNORE_FAILURE:
    case actionTypes.PAYMENT_FAILURE:
    case actionTypes.UPDATE_USER_INFO_FAILURE:
    case actionTypes.LOGIN_FAILURE:
    case actionTypes.CREATE_ACCOUNT_FAILURE:
      if (action.error) {
        setTimeout(
          () => {
            toast.error(action.error);
          }, 0)
      } else {
        setTimeout(
          () => {
            toast.error(' !    .       .');
          }, 0)
      }
      return { ...state }

    case actionTypes.UPDATE_USER_INFO_SUCCESS:
      setTimeout(
        () => {
          toast.success('!      .');
        }, 0)
      return { ...state };

    case actionTypes.LOGOUT_REQUEST:
      setTimeout(
        () => {
          toast.info('  !',
            {
              position: 'bottom-right',
            }
          )
        }, 0)
      return { ...state };

    case actionTypes.LOGOUT:
      setTimeout(
        () => {
          toast.warning('              !');
        }, 0)
      return { ...state };

    case actionTypes.PAYMENT_IGNORE_SUCCESS:
      window.location.reload();
      setTimeout(
        () => {
          toast.success('!     .           .',
            {
              autoClose: false,
              closeOnClick: false,
              draggable: false,
            }
          );
        }, 0)
      return { ...state };

    case actionTypes.PAYMENT_SUCCESS:
      setTimeout(
        () => {
          toast.info('     ...');
        }, 0)
      return { ...state };

    default:
      return state;
  }
}

export default notifications;